import axios from 'axios'
export default {
  name: 'assistantForm',
  data () {
    return {
      // taskCount: Number,
      addTask: false,
      task: {
        name: '',
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: '',
        endTime: '',
        leaderID: '',
        content: '',
        target: '',
        participants: []
      },
      taskAll: [{
        name: '',
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: '',
        endTime: '',
        leaderID: '',
        content: '',
        target: '',
        participants: []
      }],
      item: {
        name: '',
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: '',
        endTime: '',
        content: '',
        leaderID: '',
        target: '',
        participants: []
      },
      list: []
    }
  },
  methods: {
    handleAddTask () {
      this.taskAll.forEach(function (item) {
        console.log(item.name)
        console.log(item.leaderID)
      })
      this.task.name = ''
      this.task.leaderID = -1
      this.addTask = true
    },
    saveTask () {
      if (this.task.name === '') {
        this.$notify.warning({
          title: '请输入课题名称！'
        })
        return
      }
      if (parseInt(this.task.leaderID) === -1) {
        this.$notify.warning({
          title: '请选择课题负责人！'
        })
        return
      }
      this.taskAll.push({
        name: this.task.name,
        creatorID: parseInt(localStorage.getItem('id')),
        startTime: this.task.startTime,
        endTime: this.task.endTime,
        leaderID: parseInt(this.task.leaderID),
        content: this.task.content,
        target: this.task.target,
        participants: this.task.participants
      })
      this.addTask = false
      this.taskAll.forEach(function (item) {
        console.log(item.name)
        console.log(item.leaderID)
      })
    },
    getPIList () {
      axios.get(this.url + '/user/level/' + this.PI)
        .then(this.getPIListSucc)
    },
    getPIListSucc (res) {
      res = res.data
      if (res) {
        this.LeaderList = res
        this.list = this.list.concat(this.LeaderList)
      }
    },
    getPRList () {
      axios.get(this.url + '/user/level/' + this.PR)
        .then(this.getPRListSucc)
    },
    getPRListSucc (res) {
      res = res.data
      if (res) {
        this.FullList = res
        this.list = this.list.concat(this.FullList)
        console.log(this.list)
      }
    },
    getStudentList () {
      axios.get(this.url + '/user/level/' + this.student)
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      res = res.data
      if (res) {
        this.StudentList = res
      }
    },
    submit () {
      // if (this.item.leaderID === -1) {
      //   this.$notify.warning({
      //     title: '请选择项目负责人！'
      //   })
      //   return
      // }
      if (this.item.name === '') {
        this.$notify.warning({
          title: '请输入项目名称！'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/project/',
        data: JSON.stringify(this.item)
      }).then((res) => {
        if (res.status === 200) {
          const data = res.data
          alert(data.id)
          this.taskAll.forEach(function (item) {
            if (item.name !== '') {
              axios.post('http://bci.renjiwulian.com/module/', {
                name: item.name,
                creatorID: item.creatorID,
                startTime: item.startTime,
                endTime: item.endTime,
                leaderID: item.leaderID,
                content: item.content,
                target: item.target,
                participants: item.participants,
                projectID: data.id
              }).then((res) => {
                if (res.status !== 200) alert(res.data)
              }).catch((err) => {
                alert(err)
              })
            }
          })
          alert('成功创建项目！')
          this.$router.push('/project')
        } else alert(res)
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.item.leaderID = -1
    this.task.leaderID = -1
    // this.taskCount = -1
    this.getPIList()
    this.getPRList()
    this.getStudentList()
  }
}



